  transform: scaleX(-1);
}

.k-sprite {
  display: inline-block;
  width: 16px;
  height: 16px;
  overflow: hidden;
  background-repeat: no-repeat;
  font-size: 0;
  line-height: 0;
  text-align: center;
  -ms-high-contrast-adjust: none;
}

.k-image {
  display: inline-block;
}

.k-line {
  background-color: currentColor;
  border-color: currentColor;
}

.k-line-h,
.k-line-v {
  position: absolute;
}

.k-line-h {
  height: 2px;
}

.k-line-v {
  width: 2px;
}

.k-loading {
  width: 64px;
  height: 64px;
  display: block;
}
.k-loading .animate {
  animation: loading 2s infinite linear;
}

.k-loading-mask,
.k-loading-image,
.k-loading-color {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.k-loading-mask {
  z-index: 100;
}
.k-loading-mask.k-opaque .k-loading-color {
  opacity: 1;
}

.k-loading-text {
  text-indent: -4000px;
  text-align: center;
  position: absolute;
}

.k-loading-image {
  z-index: 2;
}

.k-loading-color {
  background-color: #ffffff;
  opacity: 0.3;
}

.k-i-loading {
  position: relative;
  background-color: transparent;
  box-sizing: border-box;
  color: currentColor;
}

.k-i-loading::before,
.k-i-loading::after,
.k-loading-image::before,
.k-loading-image::after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  content: "";
  box-sizing: inherit;
  border-radius: 50%;
  border-width: 0.05em;
  border-style: solid;
  border-color: currentColor;
  border-top-color: transparent;
  border-bottom-color: transparent;
  background-color: transparent;
}

.k-icon.k-i-loading::before,
.k-icon.k-i-loading::after {
  content: "";
}

.k-i-loading::before,
.k-loading-image::before {
  margin-top: -0.5em;
  margin-left: -0.5em;
  width: 1em;
  height: 1em;
  animation: k-loading-animation 0.7s linear infinite;
}

.k-i-loading::after,
.k-loading-image::after {
  margin-top: -0.25em;
  margin-left: -0.25em;
  width: 0.5em;
  height: 0.5em;
  animation: k-loading-animation reverse 1.4s linear infinite;
}

.k-loading-image::before,
.k-loading-image::after {
  content: "";
  border-width: 0.015em;
  font-size: 4em;
}

@keyframes loading {
  0% {
    stroke-dasharray: 0 251;
    stroke-dashoffset: 502;
  }
  50% {
    stroke-dasharray: 250 1;
  }
  100% {
    stroke-dasharray: 0 251;
    stroke-dashoffset: 0;
  }
}
@keyframes k-loading-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.k-resize-handle,
.k-resize-hint {
  position: absolute;
  border-color: inherit;
